import React from "react"
import { Link } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/seo"

const Word = () => (
  <Layout>
    <SEO title="Vad betyder esport betting?" />
    <div className="content-header">
      <h1>Vad betyder esport betting?</h1>
      <p>Populär betting kategori för vadslagning om pengar på datorspel.</p>
      <h3>Vilka spelbolag har esport betting på sin oddssajt?</h3>
      <p>Idag finns det flera <Link to="/spelbolag/">spelbolag</Link> som har inkluderat esport betting i deras sportbok. För att göra det hela lite enklare så hänvisar vi er till Alcasino för där finner du en komplett lista med spelbolag du kan <a href="https://alcasino.se/esport-betting/" target="_blank">betta på esport</a> hos. De går igenom allt du behöver veta för att komma igång och de har även recenserat samtliga bolag. Ta reda på ifall det finns välkomstbonus och även vilka andra sorters spel de erbjuder sina kunder.</p>
      <h3>Allmänt om esport</h3>
      <p>Gaming som det också kallas har växt otroligt mycket de senaste åren. Det finns idag skolor där du kan plugga esport. Detta har medfört att allt fler satsar på att antingen bli proffs eller en karriär som streamer. Idag finns det professionella ligor i fler spel än tidigare och detta har väckt intressen ifrån många spelbolag. Allt fler spelbolag har gjort det möjligt att betta på esport hos dem. Denna typen av betting går under samma kategori som vanlig sport. Så det är samma regler och villkor som gäller vid vanlig betting. Att spelbolagen har satsat på esport har gjort att det blivit ett stort uppsving inom esporten. Det finns idag lag som är sponsrade av spelbolag och även stora turneringar. </p>
      <br />
      <p>Innan var det inte möjligt att betta på esport hos riktiga spelbolag därför satsade datorspelare sina skins eller modeller som man säger på svenska. Om man tittar på spelet <a href="https://blog.counter-strike.net/" target="_blank">CS:GO</a> så för att få skins behöver du köpa nycklar och lådor. I lådorna finns det olika sorters skins med olika värden. Så har man rejält med tur så kan man vinna en modell som är värd mycket. Det var just dessa modeller som användes vid betting tidigare. Och bolagen som erbjöd skinsbetting var inte seriösa aktörer. Riggade spel fanns och det var väldigt många som blev lurade. Självklart fanns det seriösa sajter men de var få. Så nu när seriösa spelbolag erbjuder esport betting så jublar alla esport fantaster som tycker om att sätta en slant på datorspelsmatcher.</p>
      <Link to="/" className="back-button">Tillbaka till startsidan</Link>
    </div>
  </Layout>
)

export default Word
